<template>
    <div class="app-not-found-page">
        <div class="app-not-found-title text-xs-center">
            <div class="not-found-avatar">
                <v-icon class="not-found-avatar-icon">info</v-icon>
            </div>
            <p>页面未找到</p>
        </div>
    </div>
</template>

<script>
import {mapActions} from 'vuex';
import types from '@/store/mutation-types';
import pageLoadingMixin from '@/mixins/pageLoadingMixin';

export default {
    name: 'notFound',
    mixins: [pageLoadingMixin],
    methods: {
        ...mapActions([
            'setPageLoading'
        ])
    },
    beforeRouteEnter(to, from, next) {
        next(vm => {
            // 隐藏头部其他元素，只留下返回按钮
            vm.$store.commit(types.SET_APP_HEADER, {
                title: '页面未找到',
                show: true,
                showMenu: false,
                showBack: true,
                showLogo: false,
                actions: []
            });

            // 隐藏底部栏
            vm.$store.commit(types.SET_APP_BOTTOM_NAV, {
                show: false
            });
        });
    },
    mounted() {
        this.setPageLoading(false);
    }
};
</script>

<style lang="stylus" scoped>
    @import '~@/assets/styles/variables';
    
    $theme := {
        primary: rgb(40, 116, 240)
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    
        $material-custom := {
            bg-color: #fff
            fg-color: #000
            text-color: #000
            primary-text-percent: 0.87
            secondary-text-percent: 0.54
            disabledORhints-text-percent: 0.38
            divider-percent: 0.12
            active-icon-percent: 0.54
            inactive-icon-percent: 0.38
        }
        $material-theme := $material-custom
    


.app-not-found-title
    margin-top 50px
    color #666
    font-size 24px
    font-weight bold

.not-found-avatar-icon
    width 120px
    height 120px
    font-size 100px

</style>



